<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>npm install @inagora<span class="token operator">/</span>wd<span class="token operator">-</span>view
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>2、使用组件</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code>Vue<span class="token punctuation">.</span><span class="token function">createApp</span><span class="token punctuation">(</span><span class="token punctuation">{</span><span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">.</span><span class="token function">use</span><span class="token punctuation">(</span>WdView<span class="token punctuation">)</span><span class="token punctuation">.</span><span class="token function">mount</span><span class="token punctuation">(</span><span class="token string">'#app'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
